.new-detail-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    padding: 15px;
    gap: 40px;

    .title {
        font-size: 35px;
        color: rgba($color: #000000, $alpha: 0.7);
    }

    .nav-sub {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 50px;

        .date {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 15px 25px;
            background-color: rgb(0, 183, 255);
            color: #fff;

            .day {
                font-size: 25px;
                font-weight: 800;
            }

            .month {
                font-size: 15px;
            }
        }

        .nav-sub-child {
            display: flex;
            gap: 64px;
            padding: 25px;
            justify-content: center;
            align-items: center;
            border-bottom: 0.5px solid black;
            border-top: 0.5px solid black;
            font-size: 16px;
            font-family: fantasy;

            .category-detail {
                display: flex;
                align-items: center;
                gap: 5px;

                .categories {
                    color: rgb(127, 161, 255);
                    cursor: pointer;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

            .comments {
                display: flex;
                align-items: center;
                gap: 10px;
                color: rgb(127, 161, 255);
                cursor: pointer;

                &:hover {
                    text-decoration: underline;
                }

                svg {
                    color: #000000;
                }


            }
        }
    }

    .content-contact {
        display: flex;
        flex-direction: column;
        align-items: start;
        gap: 10px;

        .child {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
            font-size: 15px;

            .svg {
                width: 15px;
                height: 15px;
            }
        }



    }

    .hashtag {
        display: flex;
        align-items: start;
        width: 80%;
        flex-wrap: wrap;
        gap: 10px;
        font: 15px;
        color: rgb(0, 195, 255);

    }

    .reply {
        display: flex;
        flex-direction: column;
        align-items: start;


        .form-reply {
            display: flex;
            justify-content: center;
            align-items: center;

            .left-form {
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
    }

}